<template>
  <div id="tab-bar">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: "TabBar",
    components: {

    },
  }
</script>

<style scoped>
  #tab-bar{
    display: flex;    /*设置flex布局*/
    background-color: #f6f6f6;

    /*为了让TabBar在下面*/
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;

    /*添加阴影*/
    box-shadow: 0px -3px 1px rgba(100,100,100,0.1);
  }
</style>



